// @mui
import { useEffect, useState } from 'react';

import { Helmet } from 'react-helmet-async';
import { alpha } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
// components
import { useSettingsContext } from 'src/components/settings';
import { Button, Dialog, DialogActions, DialogContent, DialogTitle, FormControl, FormControlLabel, FormGroup, FormHelperText, FormLabel, Grid, InputAdornment, InputLabel, Menu, MenuItem, Modal, Select, Switch, TextField } from '@mui/material';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import AddIcon from '@mui/icons-material/Add';
import SearchIcon from '@mui/icons-material/Search';
import PhoneIcon from '@mui/icons-material/Phone';
import { color, common, decryptToken, formatDate, generateOfferCode, states } from 'src/utils/common';
import axios, { endpoints } from 'src/utils/axios';
import { constants } from 'src/utils/constant';
import { useSnackbar } from 'src/components/snackbar';
import DataTable from 'react-data-table-component';
import { DatePicker, LocalizationProvider } from '@mui/x-date-pickers';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { SingleInputDateRangeField } from '@mui/x-date-pickers-pro';
import dayjs from 'dayjs';
import utc from 'dayjs/plugin/utc';  // Import the UTC plugin


dayjs.extend(utc);  // Enable the UTC plugin

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: '70%',
  bgcolor: 'background.paper',
  borderRadius: '10px',
  boxShadow: 24,
  p: 2,
  backgroundColor: color.modalbackground,
  height: 'auto',
  overflow: 'auto',
};


// ----------------------------------------------------------------------
const initialData = {
  price: '',
  type: '',
  date: null,
  category: '',
  paymentStatus: 'PAID',
  paymentMode: '',
  bankId: ''
};


const initialErr = {
  price: false,
  date: false,
  type: false,
  category: false,
  paymentStatus: false,
  paymentMode: false,
  bankId: false
};

const styleForRecordPayment = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: '400px',
  bgcolor: color.modalbackground,
  borderRadius: '10px',
  height: "auto",
  boxShadow: 24,
  p: 2,
};

const categories = [
  'ELECTRICITY BILL',
  'REPAIR AND MAINTAINANCE',
  'INTERNET BILLS',
  'RAW MATERIAL',
  'RENT',
  'PRINTING',
  'EMPLOYEE SALARIES',
];

const incomeCategories = [
  'SALE OF OLD NEWSPAPERS',
  'SALE OF CARTOON BOXES',
  'RENTAL INCOME',
  'SALE OF UNUSED BUSINESS EQUIPMENT',
];

const paymentModes = [
  'CASH',
  'CARD',
  'UPI',
  'CHEQUE',
];

const initialRecordPaymentState = {
  id: '',
  type: '',
  balance: 0,
  amountToPay: 0,
  paymentDate: dayjs(new Date()),
  paymentMode: '',
  bank: '',
};

export default function ExpenditureView() {



  const [dateValue, setDateValue] = useState(dayjs('2022-04-17'));
  const utcDateFromBackend = '2024-01-14T18:30:00.000+00:00';  // Replace this with the UTC date from your backend
  const utcDateObject = dayjs.utc(utcDateFromBackend);
  const localDateObject = utcDateObject.local();
  // console.log("ðŸš€ ~ ExpenditureView ~ localDateObject:", localDateObject)

  const settings = useSettingsContext();
  const { enqueueSnackbar } = useSnackbar();

  // For Modal Open
  const [open, setOpen] = useState(false);
  const [recordPaymentModal, setRecordPaymentModal] = useState(false);
  const [recordPayment, setRecordPayment] = useState(initialRecordPaymentState);
  const [isLoading, setIsLoading] = useState(false);
  const [dataArray, setDataArray] = useState([]);
  const [tempArray, setTempArray] = useState([]);
  const [banks, setBanks] = useState([]);
  const [data, setData] = useState(initialData);
  const [dataErr, setDataErr] = useState(initialErr);
  const [editUser, setEditUser] = useState(false);
  const [filterCatergory, setFilterCatergory] = useState('ALL');

  // Temporary State
  const [tempData, setTempData] = useState({});

  const [searchQuery, setSearchQuery] = useState('');


  // For Actions Dropdown
  const [anchorEl, setAnchorEl] = useState(null);
  const openMenu = Boolean(anchorEl);


  // For Delete Customer
  const [openDialog, setOpenDialog] = useState(false);

  const handleOpen = () => {
    setData((prev) => ({ ...prev, date: dayjs(new Date()) }));
    setOpen(true);
    setEditUser(false);
  };

  const handleClose = () => setOpen(false);


  const fetchData = async () => {

    try {
      const decryptedToken = localStorage.getItem(constants.keyUserToken);
      const accessToken = decryptToken(decryptedToken);
      const response = await axios.post(endpoints.expenditure.list, {}, {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        }
      });
      if (response) {
        setIsLoading(false);
        setDataArray(response.data.data);
        setTempArray(response.data.data);
      }
    } catch (err) {
      enqueueSnackbar('Failed to fetch dataArray!', { variant: 'error' });
      setIsLoading(false);
    }
  };
  const fetchBanks = async () => {

    try {
      const decryptedToken = localStorage.getItem(constants.keyUserToken);
      const accessToken = decryptToken(decryptedToken);
      const response = await axios.post(endpoints.bank.list, {}, {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        }
      });
      if (response) {
        setBanks(response.data.data);
      }
    } catch (err) {
      enqueueSnackbar('Failed to fetch bank accounts!', { variant: 'error' });
    }
  };

  useEffect(() => {
    setIsLoading(true);
    fetchData();
    fetchBanks();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  // You can use setFormData to update the state when user input changes
  const handleChange = (e) => {
    const { name, value } = e.target;
    setData((prevFormData) => ({
      ...prevFormData,
      [name]: value,
    }));

    // Clear the error for the field when the user starts typing again
    setDataErr((prevErrors) => ({
      ...prevErrors,
      [name]: false,
    }));
  };

  const handleSave = async () => {
    const payload = {};
    if (!data.price) {
      setDataErr((prevErrs) => ({ ...prevErrs, price: true }))
      return;
    }
    setDataErr((prevErrs) => ({ ...prevErrs, price: false }))

    if (!data.date) {
      setDataErr((prevErrs) => ({ ...prevErrs, date: true }))
      return;
    }
    setDataErr((prevErrs) => ({ ...prevErrs, date: false }))

    if (!data.type) {
      setDataErr((prevErrs) => ({ ...prevErrs, type: true }))
      return;
    }
    setDataErr((prevErrs) => ({ ...prevErrs, type: false }))

    if (!data.category) {
      setDataErr((prevErrs) => ({ ...prevErrs, category: true }))
      return;
    }
    setDataErr((prevErrs) => ({ ...prevErrs, category: false }))

    //  Check only if data.paymentStatus is paid otherwise leave it
    if (data.paymentStatus === 'PAID') {
      if (!data.paymentMode) {
        setDataErr((prevErrs) => ({ ...prevErrs, paymentMode: true }))
        return;
      }
      setDataErr((prevErrs) => ({ ...prevErrs, paymentMode: false }));

      if (data.paymentMode !== 'CASH' && !data.bankId) {
        setDataErr((prevErrs) => ({ ...prevErrs, bankId: true }))
        return;
      };
      setDataErr((prevErrs) => ({ ...prevErrs, bankId: false }));
    };

    const utcValue = data.date.format('YYYY/MM/DD');
    payload.price = parseFloat(data.price);
    payload.date = utcValue;
    payload.category = data.category;
    payload.paymentStatus = data.paymentStatus;
    payload.type = data.type;

    if (data.paymentStatus === 'PAID') {
      payload.paymentMode = data.paymentMode;
      if (data.bankId) {
        payload.bankId = data.bankId;
      };
    }


    // If no errors, proceed with saving
    const decryptedToken = localStorage.getItem(constants.keyUserToken);
    const accessToken = decryptToken(decryptedToken);
    axios.post(endpoints.expenditure.add, payload,
      {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        }
      })
      .then((res) => {
        enqueueSnackbar('Expense created successfully!', { variant: 'success' });
        fetchData();
        handleClose();
        // Reset the error state
        setDataErr(initialErr);

        // Reset the data  state
        setData(initialData);
      }).catch((err) => {
        enqueueSnackbar(err.error || err.msg, { variant: 'error' });
      })
  };

  const handleEditDetails = () => {
    setEditUser(true);
    setAnchorEl(null);

    const editedExpData = {
      id: tempData._id,
      price: tempData.price,
      date: dayjs(tempData.date),
      type: tempData.type,
      category: tempData.category,
    };
    setData(editedExpData);
    setOpen(true);
  };

  const handleDeleteDetails = () => {
    setData((prev) => ({ ...prev, id: tempData._id }));
    setOpenDialog(true);
    setAnchorEl(null);
  };

  const handleConfirmDelete = () => {

    const decryptedToken = localStorage.getItem(constants.keyUserToken);
    const accessToken = decryptToken(decryptedToken);
    const config = {
      headers: {
        Authorization: `Bearer ${accessToken}`
      }
    };

    axios.post(endpoints.expenditure.delete, { id: data.id }, config)
      .then((res) => {
        if (res) {
          enqueueSnackbar('Offer deleted successfully!', { variant: 'success' });
          // setOpenDialog(false);
          // setData(initialData);
          // setDataErr(initialErr);
          // setTempData({});
          // fetchData();
        };
      }).catch((err) => {
        enqueueSnackbar(err.msg || err.error, { variant: 'error' });
      })
  };

  const handleUpdate = async () => {

    const payload = {};
    if (!data.date) {
      setDataErr((prevErrs) => ({ ...prevErrs, date: true }))
      return;
    }
    setDataErr((prevErrs) => ({ ...prevErrs, date: false }))

    if (!data.category) {
      setDataErr((prevErrs) => ({ ...prevErrs, category: true }))
      return;
    }
    setDataErr((prevErrs) => ({ ...prevErrs, category: false }))

    if (!data.type) {
      setDataErr((prevErrs) => ({ ...prevErrs, category: true }))
      return;
    }
    setDataErr((prevErrs) => ({ ...prevErrs, category: false }))


    const utcValue = data.date.format('YYYY/MM/DD');

    payload.date = utcValue;
    payload.type = data.type;
    payload.category = data.category;
    payload.id = data.id;

    // If no errors, proceed with saving
    const decryptedToken = localStorage.getItem(constants.keyUserToken);
    const accessToken = decryptToken(decryptedToken);
    axios.post(endpoints.expenditure.update, data,
      {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        }
      })
      .then((res) => {
        enqueueSnackbar('Expenditure updated successfully!', { variant: 'success' });
        handleClose();
        // Reset the error state
        setDataErr(initialErr);

        // Reset the data  state
        setData(initialData);

        fetchData();
      }).catch((err) => {
        enqueueSnackbar(err.error || err.msg, { variant: 'error' });
      })



  };

  const columns = [
    {
      name: 'Type',
      selector: row => row.type,
      sortable: 'true',
    },
    {
      name: 'Category',
      selector: row => row.category,
      sortable: 'true',
      width: '250px'
    },
    {
      name: 'Paid Amount',
      selector: row => row.price,
      sortable: 'true',
      width: '150px',
    },
    {
      name: 'Amount To Pay',
      selector: row => row.remainingAmount,
      sortable: 'true',
      width: '150px',
    },
    {
      name: 'Payment Mode',
      selector: row => row.payments.length === 0 ? '-' : row.payments[row.payments.length - 1].paymentID.paymentMode,
      sortable: true,
      width: '150px'
    },
    {
      name: 'Payment Status',
      selector: row => row.paymentStatus,
      sortable: 'true',
      center: 'true',
      width: '150px'
    },
    {
      name: 'Date',
      selector: row => new Date(row.date).toLocaleDateString('en-GB', { day: '2-digit', month: 'long', year: 'numeric' }),
      sortable: 'true',
      width: '150px'
    },
    {
      name: 'Actions',
      width: '20%',
      cell: (row) => (
        <>
          <Button onClick={(event) => {
            setAnchorEl(event.currentTarget);
            setTempData(row)
          }} style={{ color: color.actionButton }} variant='outlined' endIcon={<ArrowDropDownIcon />}>Action</Button>
          <Menu
            id="basic-menu"
            anchorEl={anchorEl}
            open={openMenu}
            onClose={() => {
              setAnchorEl(null);
              setTempData({});
            }}
            MenuListProps={{
              'aria-labelledby': 'basic-button',
            }}
          >
            <MenuItem onClick={() => handleEditDetails()}>Edit Details</MenuItem>
            <MenuItem onClick={() => handleDeleteDetails()}>Remove</MenuItem>
            {
              tempData.remainingAmount > 0 &&
              <MenuItem onClick={() => {
                setAnchorEl(null);
                setRecordPaymentModal(true);
                setRecordPayment((prev) => ({
                  ...prev,
                  type: tempData.type,
                  balance: tempData.remainingAmount,
                  id: tempData._id
                }));
                setTempData({});
              }}>Record Payment</MenuItem>
            }
          </Menu>
        </>
      ),
    },
  ];

  const filteredCustomers = dataArray.filter((offData) =>
    offData?.category?.toLowerCase().includes(searchQuery?.toLowerCase())
  );

  const handleRecordPayment = () => {
    const recordPaymentPayload = {};

    if (parseFloat(recordPayment.amountToPay) <= 0) {
      enqueueSnackbar('Amount to pay should be greater than 0!', { variant: 'error' });
      return;
    }
    recordPaymentPayload.id = recordPayment.id;
    recordPaymentPayload.amount = parseFloat(recordPayment.amountToPay);
    recordPaymentPayload.paymentDate = dayjs(recordPayment.paymentDate).format('YYYY-MM-DD');
    recordPaymentPayload.paymentMode = recordPayment.paymentMode;
    if (recordPayment.bank.length > 0) {
      recordPaymentPayload.bank = recordPayment.bank;
    };

    // If no errors, proceed with saving
    const decryptedToken = localStorage.getItem(constants.keyUserToken);
    const accessToken = decryptToken(decryptedToken);
    axios.post(endpoints.expenditure.recordPayment, recordPaymentPayload,
      {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        }
      })
      .then((res) => {
        enqueueSnackbar('Payment recorded successfully!', { variant: 'success' });
        setRecordPaymentModal(false);
        setRecordPayment(initialRecordPaymentState);
        fetchData();
      }).catch((err) => {
        enqueueSnackbar(err.error, { variant: 'error' });
      })

  }

  return (
    <Container maxWidth={settings.themeStretch ? false : 'xl'}>
      <Helmet>
        <title> Dashboard : Expense / Income</title>
      </Helmet>

      <Typography variant="h4"> Expense/Income </Typography>

      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginTop: '20px' }}>
        <div style={{ display: "flex", flexDirection: 'row', alignItems: "center", gap: "10px" }}>
          <TextField
            placeholder='Search'
            size='small'
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <SearchIcon />
                </InputAdornment>
              ),
            }}
          />
          <FormControl size='small' sx={{ width: '230px' }}>
            <InputLabel id="demo-simple-select-label">Select Category</InputLabel>
            <Select
              labelId="demo-simple-select-label"
              id="demo-simple-select"
              value={filterCatergory}
              label="Select Category"
              onChange={(e) => {
                setIsLoading(true);
                if (e.target.value === 'INCOME') {
                  setDataArray(tempArray.filter((record) => record.type === 'INCOME'));

                } else if (e.target.value === 'EXPENSE') {
                  setDataArray(tempArray.filter((record) => record.type === 'EXPENSE'));
                } else {
                  setDataArray(tempArray);
                }

                setFilterCatergory(e.target.value);
                setIsLoading(false);
              }}

            >
              <MenuItem value='ALL'>ALL</MenuItem>
              <MenuItem value='EXPENSE'>EXPENSE</MenuItem>
              <MenuItem value='INCOME'>INCOME</MenuItem>
            </Select>

          </FormControl>
          <LocalizationProvider dateAdapter={AdapterDayjs} >
            <SingleInputDateRangeField
              size='small'
              label="Date Range"

            />
          </LocalizationProvider>
        </div>
        <Button startIcon={<AddIcon />} variant='contained' color='primary' onClick={handleOpen}>
          ADD ENTRY
        </Button>
      </div>

      <Modal
        open={open}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <div style={{ display: 'flex', flexDirection: 'column', gap: '10px' }}>

            <Typography id="modal-modal-title" variant="h4" component="h2">
              Basic Details
            </Typography>
            <div style={{ padding: "10px", backgroundColor: 'white', borderRadius: '10px' }}>
              <Grid container spacing={2} >
                <Grid item xs={6}>
                  <TextField
                    label='Amount in rupees'
                    name='price'
                    type='number'
                    value={data.price}
                    disabled={editUser}
                    onBlur={() => {
                      if (parseFloat(data.price) <= 0) {
                        enqueueSnackbar('Expense should be greater than 0!', { variant: 'error' });
                        setData((prev) => ({ ...prev, price: 1 }))
                      };
                    }}
                    onChange={handleChange}
                    error={dataErr.price}
                    helperText={dataErr.price ? 'Please enter expense in rupees' : null}
                    fullWidth
                    required />
                </Grid>
                <Grid item xs={6}>
                  <LocalizationProvider dateAdapter={AdapterDayjs}>
                    <DatePicker
                      label="Date"
                      value={data.date}
                      format='DD/MM/YYYY'
                      // error={dataErr.date}
                      // helperText='Please enter expense date'
                      onChange={(newValue) => {
                        setData((prev) => ({ ...prev, date: newValue }))
                        setDataErr((prev) => ({ ...prev, date: false }))
                      }}
                      slotProps={{ textField: { fullWidth: true, required: true, error: dataErr.date, helperText: dataErr.date ? 'Please select expense date!.' : null } }}
                    />
                  </LocalizationProvider>
                </Grid>

                <Grid item xs={6}>
                  <FormControl fullWidth required>
                    <InputLabel id="demo-simple-select-label">Type</InputLabel>
                    <Select
                      labelId="demo-simple-select-label"
                      id="demo-simple-select"
                      value={data.type}
                      label="Type"
                      name='type'
                      onChange={(e) => {
                        setData((prev) => ({ ...prev, type: e.target.value, category: '' }))
                        setDataErr((prev) => ({ ...prev, type: false }))
                      }}
                      required
                      error={dataErr.type}
                    >
                      <MenuItem value='EXPENSE'>EXPENSE</MenuItem>
                      <MenuItem value='INCOME'>INCOME</MenuItem>
                    </Select>
                    {
                      dataErr.type &&
                      <FormHelperText sx={{ color: 'red' }}>Please Select Expense Type</FormHelperText>
                    }
                  </FormControl>
                </Grid>
                <Grid item xs={6}>
                  <FormControl fullWidth required>
                    <InputLabel id="demo-simple-select-label">Category</InputLabel>
                    <Select
                      labelId="demo-simple-select-label"
                      id="demo-simple-select"
                      value={data.category}
                      label="Category"
                      name='category'
                      disabled={!data.type}
                      onChange={(e) => {
                        setData((prev) => ({ ...prev, category: e.target.value }))
                        setDataErr((prev) => ({ ...prev, category: false }))
                      }}
                      error={dataErr.category}
                    >

                      {/* I have diffenrent categories for diffenret type if expense then use this and if income use other */}

                      {
                        data.type === 'EXPENSE' ?

                          categories.map((category, i) => (
                            <MenuItem key={i} value={category}>{category}</MenuItem>
                          ))
                          :
                          incomeCategories.map((category, i) => (
                            <MenuItem key={i} value={category}>{category}</MenuItem>
                          ))
                      }
                    </Select>
                    {
                      dataErr.category &&
                      <FormHelperText sx={{ color: 'red' }}>Please Select Expense Category</FormHelperText>
                    }
                  </FormControl>
                </Grid>

              </Grid>
            </div>

            {
              !editUser &&
              <div style={{ padding: "10px", backgroundColor: 'white', borderRadius: '10px' }}>
                <Grid container spacing={2} >
                  <Grid item xs={12}>
                    <Typography id="modal-modal-title" variant="h6" component="h2">
                      Payments
                    </Typography>
                  </Grid>



                  <Grid item xs={12} display='flex' justifyContent='flex-start' alignItems='center'>
                    <FormControl component="fieldset" variant="standard">
                      <FormControl >
                        <FormControlLabel

                          control={
                            <Switch checked={data?.paymentStatus === 'PAID'} onChange={(e) => {
                              setData((prev) => ({ ...prev, paymentStatus: e.target.checked ? 'PAID' : 'PENDING' }))
                            }} name="paymentStatus" />
                          }
                          label="Mark as Paid"
                          labelPlacement='start'
                        />
                      </FormControl>
                    </FormControl>
                  </Grid>

                  {
                    data.paymentStatus === 'PAID' &&
                    <>
                      <Grid item xs={6}>
                        <FormControl fullWidth required>
                          <InputLabel id="demo-simple-select-label">Payment Type</InputLabel>
                          <Select
                            labelId="demo-simple-select-label"
                            id="demo-simple-select"
                            value={data.paymentMode}
                            label="Payment Type"
                            name='paymentMode'
                            onChange={handleChange}
                            error={dataErr.paymentMode}
                            required
                          >
                            {
                              paymentModes.map((mode, i) => (
                                <MenuItem key={i} value={mode}>{mode}</MenuItem>
                              ))
                            }
                          </Select>
                          {
                            dataErr.paymentMode &&

                            <FormHelperText sx={{ color: 'red' }}>Please Select Expense Payment Type</FormHelperText>
                          }
                        </FormControl>
                      </Grid>
                      {
                        data.paymentMode !== 'CASH' &&
                        <Grid item xs={6}>
                          <FormControl fullWidth required>
                            <InputLabel id="demo-simple-select-label">Select Bank</InputLabel>
                            <Select
                              labelId="demo-simple-select-label"
                              id="demo-simple-select"
                              value={data.bankId}
                              label="Select Bank"
                              name='bankId'
                              onChange={(e) => {
                                console.log(e.target.value);
                                setData((prev) => ({ ...prev, bankId: e.target.value }))
                                setDataErr((prev) => ({ ...prev, bankId: false }))
                              }}
                              error={dataErr.bankId}
                              required
                            >
                              {
                                banks.map(bank =>
                                  <MenuItem key={bank._id} value={bank._id}>{bank.bankName}</MenuItem>
                                )
                              }
                            </Select>
                            {
                              dataErr.bankId &&
                              <FormHelperText sx={{ color: 'red' }}>Please Select Bank</FormHelperText>
                            }
                          </FormControl>
                        </Grid>
                      }
                    </>
                  }


                </Grid>
              </div>
            }


          </div>
          <div style={{ display: 'flex', flexDirection: 'row', gap: '10px', justifyContent: 'flex-end', padding: "10px" }}>
            <Button variant='contained' color='primary' onClick={editUser ? handleUpdate : handleSave}>
              {editUser ? 'Update' : 'Save'}
            </Button>
            <Button variant='outlined' onClick={() => {
              if (editUser) {
                setData(initialData);
                setEditUser(false);
              };
              setOpen(false);
              setTempData({});
              setData(initialData);
              setDataErr(initialErr)
            }}>
              Cancel
            </Button>
          </div>
        </Box>

      </Modal>

      <div style={{ marginTop: "20px" }}>
        <DataTable
          columns={columns}
          data={filteredCustomers}
          pagination
          progressPending={isLoading}
        />
      </div>


      {/* Dialog For Delete */}
      <Dialog open={openDialog} onClose={() => setOpenDialog(false)}>
        <DialogTitle>Remove</DialogTitle>
        <DialogContent>
          Are you sure you want to delete?
        </DialogContent>
        <DialogActions>
          <Button onClick={() => {
            setOpenDialog(false);
            setData(initialData);
            setDataErr(initialErr);
            setTempData({});
          }}>Cancel</Button>
          <Button onClick={handleConfirmDelete} variant="contained" color="error">
            Delete
          </Button>
        </DialogActions>
      </Dialog>


      <Modal id="paymentModal"
        open={recordPaymentModal}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={styleForRecordPayment}>
          <Grid container sx={{ display: 'flex', flexDirection: 'column', gap: '10px' }} >
            <Grid item xs={12}>
              <Typography variant='h6'>Customer Details</Typography>
            </Grid>
            <Grid item xs={12}>
              <FormControl fullWidth required>
                <InputLabel id="demo-simple-select-label">Type</InputLabel>
                <Select
                  labelId="demo-simple-select-label"
                  id="demo-simple-select"
                  value={recordPayment.type}
                  label="Type"
                  name='type'
                  disabled
                  required
                  sx={{ backgroundColor: 'white', borderRadius: '7px' }}
                >
                  <MenuItem value={recordPayment.type}>{recordPayment.type}</MenuItem>
                </Select>

              </FormControl>
            </Grid>
            <Grid item xs={12}>
              <TextField
                label='Balance'
                value={recordPayment.balance}
                disabled
                sx={{ backgroundColor: 'white', borderRadius: '7px' }}
                fullWidth
              />
            </Grid>
            <Grid item xs={12}>
              <Typography variant='h6'>Payment Details</Typography>
            </Grid>
            <Grid item xs={12}>
              <TextField
                label='Amount To Pay'
                value={recordPayment.amountToPay}
                onBlur={
                  () => {
                    if (recordPayment.amountToPay > recordPayment.balance) {
                      enqueueSnackbar('Amount to pay cannot be greater than balance!', { variant: 'error' });
                      setRecordPayment((prev) => ({ ...prev, amountToPay: recordPayment.balance }));
                    }
                  }
                }
                onChange={(e) => setRecordPayment((prev) => ({ ...prev, amountToPay: e.target.value }))}
                sx={{ backgroundColor: 'white', borderRadius: '7px' }}
                fullWidth
              />
            </Grid>

            <Grid item xs={12} >
              <LocalizationProvider dateAdapter={AdapterDayjs} >
                <DatePicker

                  label="Payment Date"
                  value={recordPayment.paymentDate}
                  maxDate={dayjs(new Date())}
                  onChange={(newValue) => {
                    setRecordPayment((prev) => ({ ...prev, paymentDate: newValue }))
                  }}
                  sx={{ backgroundColor: 'white', borderRadius: '7px' }}
                  format="DD/MM/YYYY"
                  slotProps={{ textField: { fullWidth: true } }}
                />
              </LocalizationProvider>
            </Grid>
            <Grid item xs={12}>
              <FormControl fullWidth >
                <InputLabel id="record-payment-mode">Select Payment Mode</InputLabel>
                <Select
                  labelId="record-payment-mode"
                  id="demo-simple-select"
                  label='Select Payment Mode'
                  sx={{ backgroundColor: 'white', borderRadius: '7px' }}
                  value={recordPayment.paymentMode}
                  onChange={(e) => setRecordPayment((prev) => ({ ...prev, paymentMode: e.target.value }))}
                >
                  {
                    [
                      'CASH',
                      'CARD',
                      'UPI',
                      'CHEQUE',
                    ].map((paymentMode, index) => (
                      <MenuItem key={index} value={paymentMode}>{paymentMode}</MenuItem>
                    ))
                  }
                </Select>
              </FormControl>
            </Grid>
            {
              recordPayment.paymentMode !== 'CASH' &&
              <Grid item xs={12}>
                <FormControl fullWidth >
                  <InputLabel id="select-bank">Select Bank</InputLabel>
                  <Select
                    labelId="select-bank"
                    id="demo-simple-select"
                    label='Select Bank'
                    value={recordPayment.bank}
                    sx={{ backgroundColor: 'white', borderRadius: '7px' }}
                    onChange={(e) => setRecordPayment((prev) => ({ ...prev, bank: e.target.value }))}
                  >
                    {
                      banks.map((bank, index) => (
                        <MenuItem key={bank._id} value={bank._id}>{bank.bankName}</MenuItem>
                      ))
                    }
                  </Select>
                </FormControl>
              </Grid>
            }
            <Grid item xs={12} sx={{ display: 'flex', justifyContent: 'flex-end', gap: '10px' }}>
              <Button
                variant='contained'
                color='primary'
                onClick={handleRecordPayment}
                disabled={
                  !recordPayment.amountToPay ||
                  !recordPayment.paymentMode ||
                  (recordPayment.paymentMode !== 'CASH' && !recordPayment.bank)
                }
              >
                Update Payment
              </Button>
              <Button
                variant='contained'
                color='error'
                onClick={() => {
                  setRecordPaymentModal(false);
                  setRecordPayment(initialRecordPaymentState);
                }}
              >
                Cancel
              </Button>

            </Grid>

          </Grid>
        </Box>
      </Modal>
    </Container>
  );
}
